.app__title{
  margin-top: 40px;
  margin-bottom: 18px;
}

.header {
  width: 100%;
  position: relative;
  justify-content: space-between;
}

.input__search{
  display: block;
  width: 100%;
  max-width: 595px;
  border: 1px solid var(--purple);
  border-radius: 5px;
  padding: 8px 16px;
  caret-color: var(--purple);
  color: var(--purple);
}

.input__text:active, .input__text:focus {
  outline: none;
  border: 2px solid var(--purple);
}

.input__text::placeholder{
  font-size: 16px;
  line-height: 100%;
  color: #c3c1e5;
}

.select__list{
  width: 100%;
  max-width: 150px;
  border: none;
  border-radius: 5px;
  padding: 10px;
  background-color: var(--purple);
  font-size: 18px;
  line-height: 100%;
  color: var(--white);
}

.select__item{
  width: 100%;
  border: 1px solid var(--purple);
  border-radius: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  background-color: var(--white);
  color: var(--purple);
}

option:hover{
  background-color: #6C63FF;
  color: var(--purple);
}

.select__item:active{
  background-color: #6b63ffad;
}

.btn__reset{
  padding: 0;
  border:none;
/* прозрачный фон */
  background-color: transparent;
  cursor: pointer;
}

.button__search{
  position: absolute;
  right: 170px;
  top: 50%;
  transform: translate(0, -50%);
}
