.modal__window {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(37, 37, 37, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
}

.window__content {
    background: white;
    min-height: 289px;
    min-width: 500px;
    padding: 18px 30px;
    border-radius: 16px;
}

.task__button{
    position: fixed;
    bottom: 45px;
    right: 5vw;
    border: none;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-color: var(--purple);
    cursor: pointer;
  }

  .window__title{
    text-align: center;
  }

  .btn__reset{
    padding: 0;
    border:none;
  /* прозрачный фон */
    background-color: transparent;
    cursor: pointer;
  }
  
  .btn{
    max-width: 110px;
    max-height: 38px;
    border-radius: 5px;
    padding: 10px 22px;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
  }

  .form__button__cancel{
    background-color: var(--white);
    color: var(--purple);
    border: 1px solid var(--purple);
  }
  
  .form__button__apply{
    background-color: var(--purple);
    color: var(--white);
  }
  
  .form{
    flex-direction: column;
  }
  
  .form__list__buttons{
    margin-top: 128px;
    justify-content: space-between;
  }
  
  .form__input{
    max-height: 38px;
    padding: 11px 16px;
    border: 1px solid var(--purple);
    border-radius: 5px;
    caret-color: var(--purple);
    color: var(--purple);
  }
  
  .form__input::placeholder{
    font-size: 16px;
    line-height: 100%;
    color: #c3c1e5;
  }