@font-face {
  font-family: "Kanit";
  src: local("Kanit-Medium"), url("../assets/fonts/Kanit-Medium.woff2") format("woff2"),
  url("../assets/fonts/Kanit-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Kanit";
  src: local("Muller-Regular"), url("../assets/fonts/Kanit-Regular.woff2") format("woff2"),
  url("../assets/fonts/Kanit-Regular.woff2") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Inter";
  src: local("Inter-Medium"), url("../assets/fonts/Inter-Medium.woff2") format("woff2"),
  url("../assets/fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Inter";
  src: local("Inter-Bold"), url("../assets/fonts/Inter-Bold.woff2") format("woff2"),
  url("../assets/fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

:root {
  --black: #252525;
  --purple:#6C63FF;
  --white: #F7F7F7;
}

html{
  box-sizing: border-box;
}

*,
*::after,
*::before{
  box-sizing: inherit ;
}

a{
  color: inherit;
  text-decoration: none;
}

img{
  max-width: 100%;
}

body{
  font-family: 'Kanit', sans-serif;
  font-weight: 500;
  background-color: var(--white);
}

.container{
  width: 750px;
  margin: 0 auto;
}

.flex{
  display: flex;
}

h1, p{
  margin: 0;
}

.list-reset{
  list-style: none;
  margin: 0;
  padding: 0;
}

.container__align{
  flex-direction: column;
  align-items: center;
}



