.align__list{
    width: 100%;
    max-width: 520px;
    margin-top: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.task__item{
    width: 100%;
    padding-top: 17px;
    padding-bottom: 17px;
    align-items: center;
    border-bottom: 1px solid var(--purple);
}

.input__checkbox{
  appearance: none;
  position: absolute;
}

.task__text{
    position: relative;
    padding-left: 43px;
    font-size: 20px;
    line-height: 100%;
    text-transform: uppercase;
    color: var(--black);
}

.task__text::before{
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 26px;
    height: 26px;
    border: 1px solid var(--purple);
  }
  
  .task__text::after{
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 26px;
    height: 26px;
    background-color: var(--purple);
    background-image: url("../../assets/images/done.svg");
    background-position: center 5px;
    background-repeat: no-repeat;
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .input__checkbox:checked + .task__text::after{
    opacity: 1;
  }


